.sa {
  opacity: 0;
  transition: 1.5s;
  &.show {
    opacity: 1;
  }
  &.sa-r {
    transform: translateX(-30px);
    &.show {
      opacity: 1;

      transform: translateX(0);
    }
  }
  &.sa-l {
    transform: translateX(30px);
    &.show {
      opacity: 1;
      transform: translateX(0);
    }
  }
  &.sa-up {
    transform: translateY(50px);
    &.show {
      opacity: 1;
      transform: translateY(0);
    }
  }
  &.sa-down {
    transform: translateY(-50px);
    &.show {
      opacity: 1;
      transform: translateY(0);
    }
  }
}
