#maincontent {
  .p-point {
    background-color: #e8deca;
    padding: 80px 0;
    @include sp {
      padding: cl(55) 0 cl(65);
    }
    .c-head {
      margin-bottom: 30px;
    }
    &__list {
      display: flex;
      gap: 15px;
      @include sp {
        display: block;
      }
    }
    &__list-item {
      width: calc(calc(100% - 30px) / 3);
      @include sp {
        width: 100%;
      }
      & + .p-point__list-item {
        @include sp {
          margin-top: 16px;
        }
      }
    }
    &__list-link {
      background-color: #fffffa;
      padding: 30px 30px 40px;
      transition: 0.3s;
      display: block;
      text-align: center;
      box-shadow: 0 7px 16px #d0c1a2;
      border-radius: 16px;
      overflow: hidden;
      @include sp {
        padding: cl(25);
      }
      &:hover {
        box-shadow: 0 7px 16px #fff;
      }
    }
    &__list-img {
      width: min(202px, vw(202));
      margin-bottom: min(25px, vw(25));
      @include sp {
        width: cl(156);
        margin-bottom: cl(20);
      }
    }
    &__list-txt {
      font-size: min(20px, vw(20));
      margin-bottom: min(35px, vw(35));
      @include fw-gothic();
      @include sp {
        font-size: cl(16);
      }
    }
    &__arrow {
      width: min(28px, vw(28));
      margin: 0 auto;
      @include sp {
        width: cl(21);
      }
    }
  }
}
