// triumph基本設定
html {
  scroll-behavior: smooth;
  scroll-padding-top: 100px;

  @include sp {
    scroll-padding-top: 190px;
  }
}

// headerのお知らせに干渉しないように
header {
  &.triumph {
    .stroke-width {
      stroke: #000;
    }
  }
}

#maincontent {
  color: $colorBlack;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 300;
  scroll-padding-top: 80px;
  overflow-x: hidden;

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
    color: $mainGold;
    font-family: $triumphFont;
  }

  button {
    padding: 0;
    background-color: transparent;
    border: none;
  }

  a {
    &:hover {
      text-decoration: none;
    }
  }

  img {
    width: 100%;
  }

  br {
    &.pc {
      display: block;

      @include sp {
        display: none;
      }
    }

    &.sp {
      display: none;

      @include sp {
        display: block;
      }
    }
  }
}
