#maincontent {
  .p-smooth {
    padding-bottom: 280px;
    overflow: hidden;
    @include sp {
      padding-bottom: cl(120);
    }
    &__flex {
      position: relative;
      &::before,
      &::after {
        content: "";
        position: absolute;
        display: block;
        background: url(#{$img-path}/smooth_bg1.webp) no-repeat;
        background-size: 100%;
      }
      &::before {
        width: min(1655px, vw(1655));
        height: min(615px, vw(615));
        top: 157px;
        right: calc(100% - 52px);
        @include sp {
          background-image: url(#{$img-path}/smooth_bg1-sp.webp);
          width: cl(375);
          top: cl(168);
          right: 0;
        }
      }
      &::after {
        width: min(1375px, vw(1375));
        height: min(1023px, vw(1023));
        bottom: -110px;
        left: calc(100% - 248px);
        transform: rotate(4deg);
        background-image: url(#{$img-path}/smooth_bg3.webp);
        @include sp {
          background-image: url(#{$img-path}/smooth_bg2-sp.webp);
          width: cl(306);
          height: cl(161);
          bottom: cl(-49);
          left: 0;
          transform: rotate(0);
        }
      }
    }
    &__flex2-item {
      @include sp {
        position: relative;
        &::before {
          content: "";
          position: absolute;
          display: block;
          background: url(#{$img-path}/smooth_bg3-sp.webp) no-repeat;
          background-size: 100%;
          width: cl(375);
          height: cl(138);
          bottom: cl(29);
          right: cl(-70);
        }
      }
    }
    &__flex-item {
      position: relative;
      &::before {
        content: "";
        position: absolute;
        display: block;
        background: url(#{$img-path}/smooth_bg2.webp) no-repeat;
        background-size: 100%;
      }
      &::before {
        width: min(446px, vw(446));
        height: min(481px, vw(481));
        bottom: -75px;
        right: -103px;
        @include sp {
          display: none;
        }
      }
    }
    &__img {
      width: min(1030px, vw(1030));
      margin: 0 auto 130px;
      @include sp {
        width: 100%;
        margin-bottom: cl(70);
      }
    }
  }
}
