#maincontent {
  .c-lineUp {
    width: min(1020px, 100%);
    margin: 0 auto;
    text-align: center;
    @include sp {
      width: 100%;
      padding: 0 cl(35);
    }
    &--arrange {
      width: min(918px, vw(918));
    }
    &--smooth {
      width: min(954px, vw(954));
    }
    a {
      display: block;
    }
    &__head {
      font-family: "triumph grand", "armochi", sans-serif;
      margin-bottom: min(60px, vw(60));
      @include sp {
        font-size: cl(28);
        margin-bottom: cl(40);
      }
      &--lead {
        display: block;
        font-size: min(20px, vw(20));
        font-weight: 500;
        @include fw-gothic();
        @include sp {
          font-size: cl(16);
          margin-top: cl(5);
        }
      }
    }
    &__grid {
      display: grid;
      // grid-template-columns: repeat(3, 1fr);
      gap: min(140px, vw(140));
      align-items: flex-end;
      justify-content: center;
      @include sp {
        gap: cl(40);
      }
    }
    &__grid--relax {
      grid-template-columns: min(242px, vw(242)) repeat(2, min(249px, vw(249)));
      @include sp {
        grid-template-columns: cl(131) repeat(auto-fit, minmax(100px, 1fr));
        grid-template-areas: "a a" "b c";
      }
      & .c-lineUp__grid-cont1 {
        @include sp {
          width: cl(131);
          margin: 0 auto;
        }
      }
    }
    &__grid--smooth {
      grid-template-columns: min(240px, vw(240)) min(258px, vw(258)) min(
          240px,
          vw(240)
        );
      @include sp {
        grid-template-columns: cl(131) repeat(auto-fit, minmax(100px, 1fr));
        grid-template-areas: "a a" "b c";
        gap: cl(30);
      }
      & .c-lineUp__grid-cont1 {
        @include sp {
          width: cl(137);
          margin: 0 auto;
        }
      }
    }
    &__grid-cont1 {
      @include sp {
        grid-area: a;
      }
    }
    &__grid-cont2 {
      @include sp {
        grid-area: b;
      }
    }
    &__grid-cont3 {
      @include sp {
        grid-area: c;
      }
    }
    &__slider {
      margin-bottom: 20px;
      @include sp {
        margin-bottom: cl(10);
      }
    }
    &__slider-item {
    }
    &__grid-txt {
      @include fw-gothic();
      font-size: min(16px, vw(16));
      margin-bottom: 5px;
      @include sp {
        font-size: cl(14);
        margin-bottom: 0;
      }
    }
    &__grid-price {
      @include fw-gothic();
      font-size: min(16px, vw(16));
      @include sp {
        font-size: cl(14);
      }
    }

    // flex
    &__flex {
      display: flex;
      justify-content: center;
      align-items: flex-end;
      margin-bottom: 50px;
      gap: min(124px, vw(124));
      @include sp {
        flex-wrap: wrap;
        gap: cl(50);
        margin-bottom: cl(30);
      }
    }
    &__flex-cont {
      width: min(235px, vw(235));
      @include sp {
        width: calc(calc(100% - cl(50)) / 2);
      }
    }
    &__flex2 {
      display: flex;
      align-items: flex-end;
      @include sp {
        flex-wrap: wrap;
        gap: cl(30) cl(50);
        margin-bottom: cl(30);
        justify-content: center;
      }
      .c-lineUp__slider-item {
        display: flex;
        align-items: flex-end;
      }
      // ミディショーツのみ
      .c-lineUp__slider-item--arrange {
        margin-bottom: min(40px, vw(40));
        @include sp {
          margin-bottom: 0;
        }
      }
    }
    &__flex2-cont {
      display: flex;
      flex-direction: column;
      @include sp {
        width: calc(calc(100% - cl(50)) / 2);
        margin: 0;
      }
    }
    &__flex2-cont1 {
      width: min(207px, vw(207));
      margin-right: min(144px, vw(144));
    }
    &__flex2-cont2 {
      width: min(198px, vw(198));
      margin-right: min(122px, vw(122));
    }
    &__flex2-cont3 {
      width: min(249px, vw(249));
    }
    &__dots {
      position: relative;
      bottom: 0;
      margin-top: 30px;
      @include sp {
        margin-top: cl(8);
      }
      &.slick-dots li {
        width: 13px;
        height: 13px;
        border-radius: 50%;
        margin: 0 4px;
        @include sp {
          width: cl(10);
          height: cl(10);
          margin: 0 3px;
        }
        &:hover {
          opacity: 0.7;
        }
      }
      &.slick-dots li button {
        width: 100%;
        height: 100%;
      }
      &.slick-dots li button:before {
        content: "";
        width: 100%;
        height: 100%;
        color: transparent;
        background-color: #000;
        opacity: 1;
        border-radius: 50%;
      }
      &--relax {
        li {
          &:first-child {
            font-size: 20px;
            & button::before {
              background-color: #c5d6e2;
            }
          }
          &:nth-child(2) {
            & button::before {
              background-color: #817e5f;
            }
          }
          &:nth-child(3) {
            button::before {
              background-color: #e8dfd8;
              border: 1px solid #acacac;
            }
          }
        }
      }
      &--arrange {
        li {
          &:first-child {
            & button::before {
              background-color: #e8dfd8;
              border: 1px solid #acacac;
            }
          }
          &:nth-child(2) {
            & button::before {
              background-color: #e7c2c8;
            }
          }
          &:last-child {
            button::before {
              background-color: #3e393b;
            }
          }
        }
      }
      &--smooth {
        li {
          &:first-child {
            & button::before {
              background-color: #e9e8c4;
              border: 1px solid #acacac;
            }
          }
          &:nth-child(2) {
            & button::before {
              background-color: #c68472;
            }
          }
          &:last-child {
            button::before {
              background-color: #4c5063;
            }
          }
        }
      }
    }
  }
}
