#maincontent {
  .p-arrange {
    padding-bottom: min(240px, vw(240));
    overflow: hidden;
    @include sp {
      padding-bottom: cl(160);
    }
    &__img {
      width: min(1260px, vw(1260));
      margin: 0 auto 170px;
      @include sp {
        padding: 0 cl(15);
        margin-bottom: cl(60);
        width: 100%;
      }
    }
    &__flex {
      position: relative;
      &::before,
      &::after {
        content: "";
        position: absolute;
        display: block;
        background: url(#{$img-path}/arrange_bg1.webp) no-repeat;
        background-size: 100%;
      }
      &::before {
        width: min(1375px, vw(1375));
        height: min(1023px, vw(1023));
        top: -214px;
        right: calc(100% - 195px);
        transform: rotate(-4deg);
        @include max-screen($widthBaseSP) {
          top: calc(calc(-145 / 768) * 100vw);
          right: calc(100% - calc(calc(197 / 768) * 100vw));
        }
        @include sp {
          display: none;
        }
      }
      &::after {
        background-image: url(#{$img-path}/arrange_bg3.webp);
        background-size: 100%;
        width: min(1497px, vw(1497));
        height: min(1094px, vw(1094));
        bottom: min(-512px, vw(-326));
        left: calc(100% - 45px);
        @include max-screen($widthBaseSP) {
          bottom: calc(calc(-345 / 768) * 100vw);
          right: calc(100% - calc(calc(197 / 768) * 100vw));
        }
        @include sp {
          background-image: url(#{$img-path}/arrange_bg1-sp.webp);
          width: cl(329);
          height: cl(146);
          bottom: cl(-34);
          left: cl(46);
          transform: rotate(0);
        }
      }
    }
    &__flex2-item {
      @include sp {
        position: relative;
        &::before {
          content: "";
          position: absolute;
          display: block;
          background: url(#{$img-path}/arrange_bg3-sp.webp) no-repeat;
          background-size: 100%;
          width: cl(375);
          height: cl(196);
          bottom: 0;
          right: cl(-69);
        }
      }
    }
    &__flex-item {
      position: relative;
      &::before {
        content: "";
        position: absolute;
        display: block;
        background: url(#{$img-path}/arrange_bg2.webp) no-repeat;
        background-size: 100%;
      }
      &::before {
        width: min(446px, vw(446));
        height: min(481px, vw(481));
        bottom: -76px;
        right: 173px;
        @include sp {
          display: none;
        }
      }
    }
  }
}
