#maincontent {
  .p-relax {
    padding: min(240px, vw(240)) 0;
    overflow: hidden;
    @include sp {
      padding: cl(120) 0 cl(160);
    }
    &__flex {
      position: relative;
      &::before,
      &::after {
        content: "";
        position: absolute;
        display: block;
        background: url(#{$img-path}/relax_bg1.webp) no-repeat;
        background-size: 100%;
        @include sp {
          background-image: url(#{$img-path}/relax_bg1-sp.webp);
        }
      }
      &::before {
        width: min(1655px, vw(1655));
        height: min(615px, vw(615));
        top: 157px;
        right: calc(100% - 52px);
        @include sp {
          width: cl(64);
          top: cl(43);
          left: 0;
        }
      }
      &::after {
        width: min(1375px, vw(1375));
        height: min(1023px, vw(1023));
        bottom: -110px;
        left: calc(100% - 248px);
        transform: rotate(4deg);
        background-image: url(#{$img-path}/relax_bg3.webp);
        @include sp {
          background-image: url(#{$img-path}/relax_bg2-sp.webp);
          bottom: cl(-48);
          width: cl(329);
          height: cl(149);
          left: 0;
          transform: rotate(0);
        }
      }
    }
    &__flex-item {
      position: relative;
      &::before {
        content: "";
        position: absolute;
        display: block;
        background: url(#{$img-path}/relax_bg2.webp) no-repeat;
        background-size: 100%;
        @include sp {
          background-image: url(#{$img-path}/relax_bg2-sp.webp);
        }
      }
      &::before {
        width: min(446px, vw(446));
        height: min(481px, vw(481));
        bottom: -75px;
        right: -103px;
        @include sp {
          display: none;
        }
      }
    }
    &__flex2-item {
      @include sp {
        position: relative;
        &::before {
          content: "";
          background: url(#{$img-path}/relax_bg3-sp.webp) no-repeat;
          background-size: 100%;
          width: calc(100vw + cl(30));
          height: 100vw;
          top: 85px;
          left: 50%;
          transform: translate(-50%, 0);
          display: block;
          position: absolute;
        }
      }
    }
    &__img {
      width: min(918px, vw(918));
      margin: 0 auto min(142px, vw(142));
      @include sp {
        width: cl(330);
        margin-bottom: cl(60);
      }
    }
  }
}
