#maincontent {
  .p-campaign {
    padding: 120px 0 160px;
    background-color: #fffdf4;
    @include sp {
      padding: cl(70) cl(25);
    }
    &__inner {
      width: min(960px, vw(960));
      margin: 0 auto;
      @include sp {
        width: 100%;
      }
    }
    &__txt {
      font-size: min(20px, vw(20));
      color: #816b2e;
      margin-bottom: 40px;
      text-align: center;
      @include fw-gothic(500);
      @include sp {
        font-size: cl(16);
        color: #000;
        text-align: left;
        line-height: 1.6;
        font-weight: 400;
        margin-bottom: cl(30);
      }
    }
    &__flex {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      gap: min(80px, vw(80));
      @include sp {
        display: block;
      }
    }
    &__flex-head {
      font-size: min(20px, vw(20));
      color: #816b2e;
      margin-bottom: 10px;
      @include fw-gothic(500);
      @include sp {
        font-size: cl(20);
        font-feature-settings: "palt";
        letter-spacing: 0;
        margin-bottom: cl(10);
      }
    }
    &__flex-img {
      width: min(313px, vw(313));
      @include sp {
        width: 100%;
        margin-bottom: cl(30);
        padding: 0 cl(10);
        box-sizing: border-box;
        display: block;
      }
    }
    &__flex-txt {
      font-size: min(16px, vw(16));
      color: #000000;
      margin-bottom: 50px;
      line-height: 1.8;
      @include fw-gothic();
      @include sp {
        font-size: cl(16);
        margin-bottom: cl(30);
      }
    }
    &__head {
      font-size: min(28px, vw(28));
      color: #d4ab47;
      margin-bottom: 10px;
      @include fw-gothic(500);
      @include sp {
        font-size: cl(25);
        text-align: center;
        line-height: 1.3;
        margin-bottom: cl(20);
      }
      &--main {
        margin-right: 30px;
        @include sp {
          margin-right: 0;
          display: block;
        }
      }
      &--num {
        font-size: min(50px, vw(50));
        @include sp {
          font-size: cl(44);
        }
      }
    }
    &__lead {
      font-size: min(20px, vw(20));
      color: #816b2e;
      margin-bottom: 10px;
      @include fw-gothic(500);
      @include sp {
        font-size: cl(20);
        margin-bottom: cl(10);
      }
    }
    &__list {
      li {
        font-size: min(14px, vw(14));
        @include fw-gothic();
        @include sp {
          font-size: cl(14);
        }
      }
    }
  }
}
