$colorBlack: #2f2f2f;
$colorWhite: #ffffff;
$colorGray: #f2f2f2;
$colorRed: #e40032;
$mainGold: #816b2e;
$lightGold: #60360b;
$bgGold: #eeeae2;

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeUp {
  0% {
    opacity: 0;
    transform: translateY(-30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

#maincontent {
  .p-mv {
    background-color: $bgGold;
    padding: 0 0 280px;
    overflow: hidden;
    position: relative;
    @include sp {
      padding-bottom: cl(120);
    }
    &::before,
    &::after {
      content: "";
      position: absolute;
      background: url(#{$img-path}/mv_bg1.webp) no-repeat;
      background-size: 100%;
      display: block;
    }
    &::before {
      width: min(237px, vw(237));
      height: min(266px, vw(266));
      top: 0;
      right: 0;
      @include sp {
        width: cl(92);
        height: cl(82);
        background-image: url(#{$img-path}/mv_bg1-sp.webp);
      }
    }
    &::after {
      background-image: url(#{$img-path}/mv_bg3.webp);
      width: 155px;
      height: 244px;
      bottom: 0;
      left: 0;
      @include sp {
        width: cl(64);
        height: cl(106);
        background-image: url(#{$img-path}/mv_bg3-sp.webp);
      }
    }
    &__inner {
      width: min(1254px, 100%);
      margin: 0 auto;
      padding-top: 40px;
      @include sp {
        padding-top: cl(55);
      }
    }
    &__grid {
      display: flex;
      position: relative;
      margin-bottom: 130px;
      @include sp {
        display: block;
        margin-bottom: cl(20);
      }
    }
    &__grid-img {
      width: min(706px, vw(706));
      position: relative;
      @include sp {
        width: 100%;
      }
      &::before {
        content: "";
        position: absolute;
        background: url(#{$img-path}/mv_bg2.webp) no-repeat;
        background-size: 100%;
        display: block;
        width: min(803px, vw(803));
        height: min(1031px, vw(1031));
        top: min(531px, vw(531));
        left: min(592px, vw(592));
        @include sp {
          display: none;
        }
      }
      img {
        position: relative;
      }
    }
    &__grid-cont {
      position: absolute;
      top: min(300px, vw(300));
      right: min(60px, vw(60));
      text-align: center;
      @include sp {
        position: unset;
        margin-bottom: cl(25);
      }
    }
    &__head {
      font-size: min(54px, vw(54));
      margin-bottom: min(40px, vw(40));
      @include sp {
        font-size: cl(32);
        margin-bottom: cl(15);
      }
    }
    &__img {
      width: min(200px, vw(200));
      @include sp {
        width: cl(124);
      }
    }
    &__subTxt {
      font-size: min(16px, vw(16));
      color: #af931d;
      @include fw-gothic(500);
      @include sp {
        font-size: cl(16);
        text-align: center;
        margin-bottom: cl(5);
      }
    }
    &__area {
      width: min(1060px, 100%);
      margin: 0 auto;
    }

    &__txt {
      text-align: center;
      font-size: min(20px, vw(20));
      line-height: 2;
      color: #60360b;
      margin-bottom: 190px;
      @include fw-min();
      @include sp {
        font-size: cl(14);
        line-height: 2.2;
        margin-bottom: cl(75);
        padding: 0 cl(25);
      }
      & p + p {
        margin-top: min(35px, vw(35));
        @include sp {
          margin-top: cl(20);
        }
      }
    }
    &__bnr {
      transition: 0.2s;
      box-sizing: border-box;
      display: block;
      @include sp {
        padding: 0 cl(35);
      }
      &:hover {
        opacity: 0.8;
      }
    }
  }
}
