#maincontent {
  @keyframes loop {
    0% {
      transform: translate(0, 0);
    }
    100% {
      transform: translate(-50%, 0);
    }
  }

  .p-coordination {
    background-color: #e8deca;
    padding: 135px 0 80px;
    overflow: hidden;
    @include sp {
      padding: cl(80) 0 cl(100);
    }
    &__img {
      width: min(1072px, vw(1072));
      margin: 0 auto;
      @include sp {
        // margin-left: cl(25);
        width: 100%;
      }
    }
    &__img-flex {
      display: flex;
      @include sp {
        width: calc(calc(cl(698) * 2));
        padding-left: cl(24);
      }
      &.show {
        @include sp {
          animation: loop 25s linear 0s infinite;
        }
      }
      img {
        @include sp {
          width: cl(698) !important;
          // &.second {
          //   margin-left: cl(-55);
          // }
        }
      }
    }
    // & .slick-slide {
    //   display: flex !important;
    //   width: cl(745) !important;
    // }
  }
}
