.sa {
  opacity: 0;
  transition: 1.5s;
}
.sa.show {
  opacity: 1;
}
.sa.sa-r {
  transform: translateX(-30px);
}
.sa.sa-r.show {
  opacity: 1;
  transform: translateX(0);
}
.sa.sa-l {
  transform: translateX(30px);
}
.sa.sa-l.show {
  opacity: 1;
  transform: translateX(0);
}
.sa.sa-up {
  transform: translateY(50px);
}
.sa.sa-up.show {
  opacity: 1;
  transform: translateY(0);
}
.sa.sa-down {
  transform: translateY(-50px);
}
.sa.sa-down.show {
  opacity: 1;
  transform: translateY(0);
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeUp {
  0% {
    opacity: 0;
    transform: translateY(-30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
