#maincontent {
  .p-catalog {
    padding: 280px 0;
    @include sp {
      padding: cl(60) cl(25);
    }
    &__link {
      display: block;
      width: min(955px, vw(955));
      margin: 0 auto;
      transition: 0.3s;
      @include sp {
        width: 100%;
      }
      &:hover {
        opacity: 0.8;
      }
    }
  }
}
