#maincontent {
  .c-flex {
    width: min(1060px, vw(1060));
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: min(50px, vw(50));
    @include sp {
      width: 100%;
      flex-direction: column-reverse;
      padding: 0 cl(25);
      gap: 0;
    }
    &--reverse {
      flex-direction: row-reverse;
    }
    &__item {
      width: min(520px, vw(520));
      position: relative;
      z-index: 1;
      @include sp {
        width: 100%;
        margin-bottom: cl(100);
      }
      img {
        position: relative;
        z-index: 1;
      }
    }
    &__txt {
      @include sp {
        margin-bottom: cl(20);
      }
    }
    &__head {
      font-size: min(50px, vw(50));
      letter-spacing: min(1.3px, vw(1.3));
      text-align: center;
      margin-bottom: 5px;
      @include sp {
        font-size: cl(28);
        margin-bottom: cl(5);
        letter-spacing: 1px;
      }
    }
    &__txt-lead {
      font-size: min(20px, vw(20));
      color: #816b2e;
      font-weight: 500;
      letter-spacing: 0.5px;
      @include sp {
        font-size: cl(16);
        text-align: center;
      }
    }
  }
  .c-flex2 {
    width: min(1060px, vw(1060));
    margin: 0 auto;
    display: flex;
    align-items: center;
    gap: min(80px, vw(80));
    margin-bottom: min(190px, vw(190));
    @include sp {
      width: 100%;
      flex-direction: column;
      padding: 0 cl(25);
      gap: cl(25);
      margin-bottom: cl(100);
    }
    &--reverse {
      flex-direction: row-reverse;
    }
    &__item {
      width: min(336px, vw(336));
      margin-top: min(-60px, vw(-60));
      @include sp {
        margin-top: 0;
        width: cl(235);
      }
      img {
        position: relative;
        z-index: 1;
      }
    }
    &__txt {
      width: min(520px, vw(520));
      @include sp {
        width: 100%;
      }
    }
    .c-flex__head {
      margin-bottom: 30px;
      text-align: left;
      line-height: 1;
      @include sp {
        margin-bottom: cl(20);
        text-align: center;
      }
    }
    &__head {
    }
    &__txt-lead {
      font-size: min(16px, vw(16));
      line-height: 2;
      @include fw-gothic();
      @include sp {
        font-size: cl(14);
        line-height: 2.2;
      }
    }
  }
}
