@font-face {
  font-family: triumph grand;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(../fonts/TriumphGrand-Roman.woff2) format("woff2");
}
@font-face {
  font-family: armochi;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(../fonts/armochic90jp_bd.woff2) format("woff2");
}
.brand-font-jp {
  font-family: armochi;
  letter-spacing: -0.02em;
}
.brand-font-en {
  font-family: triumph grand;
}

.p-contents {
  background-color: #eeeae2;
  p {
    margin: 0;
  }
  ul {
    padding: 0;
    margin: 0;
    margin-block-start: 0;
    margin-block-end: 0;
    padding-inline-start: 0;
    list-style-type: none;
  }
  .slick-dotted.slick-slider {
    margin-bottom: 0;
  }
  .u-pc_none {
    display: none !important;
    @include sp {
      display: block !important;
    }
  }
  .u-sp_none {
    display: block !important;
    @include sp {
      display: none !important;
    }
  }
}

#maincontent {
  .l-inner {
    width: min(1110px, vw(1110));
    margin: 0 auto;
    @include sp {
      width: 100%;
      padding: 0 cl(35);
    }
  }
  .c-head {
    font-size: min(48px, vw(48));
    margin-bottom: min(60px, vw(60));
    text-align: center;
    line-height: 1.4;
    @include sp {
      font-size: cl(28);
      margin-bottom: cl(30);
      line-height: 1.3;
    }
    &--big {
      font-size: min(50px, vw(50));
    }
    &--mb40 {
      margin-bottom: min(40px, vw(40));
    }
    &--mb0 {
      margin-bottom: 0;
      @include sp {
        margin-bottom: cl(20);
      }
    }
  }
  .p-fixBtn {
    position: fixed;
    bottom: 10px;
    right: 10px;
    z-index: 1;
    width: min(340px, vw(340));
    cursor: pointer;
    transition: 0.2s;
    opacity: 0;
    @include sp {
      width: cl(187);
    }
    &.is-fadeIn {
      opacity: 1;
    }
    &:hover {
      opacity: 0.8;
    }
  }
}

// TOPへ戻るボタン削除
footer .back-to-top {
  display: none !important;
}
